@use "global" as *;
.card {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
  padding: rem(16) rem(16) rem(24);
  background-color: $white;
}

.card__body {
  margin-top: rem(24);
}

.card__date {
  font-family: $english-font-family;
  font-size: rem(12);
  line-height: 1.5;
}

.card__title {
  font-size: rem(16);
  font-weight: $medium;
  line-height: 1.5;
  padding-bottom: rem(4);
  border-bottom: 1px solid $greenaccent;
}

.card__text {
  margin-top: rem(16);
}

.card__inner {
  display: flex;
  align-items: flex-end;
}
.card__meta {
  margin-right: rem(13);
}
.card__age {
  font-size: rem(12);
  line-height: 1.5;
}
