@charset "UTF-8";
body {
  font-family: "Noto Sans JP";
  color: #0d2936;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.about {
  padding: 5rem 0;
}

.about__image {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: flex-end;
  margin-top: 3.125rem;
}

.about__image img {
  max-width: 17.5625rem;
  width: 100%;
}

.about__image::before {
  content: url(../images/about_2.jpg);
  position: absolute;
  z-index: 10;
  left: 0;
  top: -1.625rem;
}

.about__title {
  font-family: "Gotu";
  font-size: 2.5rem;
  color: #408f95;
  line-height: 1;
  margin-top: 2.5rem;
}

.about__text {
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 1rem;
}

.about__btn {
  margin-top: 2.5rem;
  text-align: center;
}

.blog {
  padding: 5rem 0;
}
@media screen and (min-width: 768px) {
  .blog {
    background: url(../images/blog-bg.jpg) no-repeat;
    background-size: contain;
  }
}

.blog__items {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
@media screen and (min-width: 768px) {
  .blog__items {
    flex-direction: row;
    margin-top: 5rem;
  }
}

.blog__btn {
  margin-top: 2.5rem;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .blog__btn {
    margin-top: 5rem;
  }
}

.campaign__items {
  margin-top: 1.5rem;
}

.campaign__btn {
  text-align: center;
  margin-top: 2.5rem;
}

.campaign__item {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
}

.campaign__meta {
  padding: 1rem 1rem 1.5rem 1rem;
}

.campaign__title {
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #408f95;
  margin-top: 0.25rem;
}

.campaign__text {
  font-size: 0.875rem;
  line-height: 1.71428;
  font-weight: 400;
  text-align: center;
  margin-top: 0.5rem;
}

.campaign__detail {
  font-family: "Lato";
  font-weight: bold;
  font-size: 2rem;
  color: #408f95;
  text-align: center;
  line-height: 1;
  margin-top: 0.5rem;
}

.campaign__detail span {
  color: #0d2936;
  font-size: 1.25rem;
  position: relative;
  display: inline-block;
  margin-right: 1rem;
}

.campaign__detail span::before {
  position: absolute;
  content: "";
  display: block;
  transform: rotate(0);
  background-color: #c94800;
  width: 100%;
  height: 0.1875rem;
  top: 50%;
  left: 0;
}

.footer {
  background-color: #408f95;
  color: #fff;
  padding-top: 3rem;
  padding-bottom: 3.5rem;
}

@media screen and (min-width: 768px) {
  .footer__head {
    display: flex;
    align-items: flex-end;
  }
}

.footer__logo {
  max-width: 7.5rem;
  width: 100%;
  aspect-ratio: 120/45;
}
@media screen and (min-width: 768px) {
  .footer__logo {
    max-width: 12.5rem;
    aspect-ratio: 200/75;
  }
}

.footer__logo img {
  width: 100%;
  height: 100%;
}

.footer__sns {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .footer__sns {
    margin-left: 2.5rem;
  }
}

.footer__snslink {
  width: 1.5rem;
}

.footer-nav__lists {
  display: grid;
  grid-template-rows: repeat(2, auto);
  grid-auto-flow: column;
  gap: 2.5rem 1rem;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .footer-nav__lists {
    grid-template-rows: repeat(1, auto);
  }
}

.footer-nav__item {
  font-weight: 700;
  line-height: 1.125;
  background-image: url(../images/seastar.png);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 1.125rem;
  padding-left: 1.375rem;
}

.footer-nav__item + .footer-nav__item {
  margin-top: 2.5rem;
}

.footer-nav__subitem {
  font-weight: 400;
  line-height: 1.125;
  margin-top: 1.5rem;
}

.footer__copyright {
  font-family: "Gotu";
  text-align: center;
  margin-top: 2.5rem;
  line-height: 1.5;
  font-size: 0.75rem;
}
@media screen and (min-width: 768px) {
  .footer__copyright {
    margin-top: 5rem;
  }
}

.header {
  background-color: rgba(13, 41, 54, 0.68);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.9375rem;
  height: 5rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
}
@media screen and (min-width: 768px) {
  .header {
    padding: 0 2.5rem;
  }
}

.header__logo {
  width: 6.375rem;
  height: 2.375rem;
  position: relative;
  z-index: 999;
}

.information {
  background-color: #ddf0f1;
  padding: 5rem 0;
}

.information__content {
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .information__content {
    display: flex;
    margin-top: 2.5rem;
    align-items: center;
  }
}

.information__body {
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .information__body {
    width: 50%;
    padding-left: 3rem;
    margin-top: 0;
  }
}

@media screen and (min-width: 768px) {
  .information__image {
    width: 50%;
  }
}

.information__title {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.2;
  border-bottom: 1px solid #408f95;
  padding-bottom: 0.25rem;
}

.information__text {
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 0.5rem;
}

.information__btn {
  margin-top: 2.5rem;
  text-align: center;
}

.pc-nav {
  display: none;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .pc-nav {
    display: block;
  }
}

.pc-nav__items {
  display: flex;
  align-items: center;
  height: inherit;
  color: #fff;
  gap: 2.5rem;
}

.pc-nav__item {
  font-family: "Gotu";
  font-size: 1.125rem;
  text-align: center;
  text-transform: capitalize;
}

.pc-nav__link span {
  display: block;
  font-size: 0.625rem;
  line-height: 1.5;
}

.sp-nav {
  display: none;
  gap: 0.5rem;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #408f95;
  z-index: 100;
  color: #fff;
  padding-top: 6.5rem;
  padding-left: 0.9375rem;
}

.sp-nav__inner {
  display: flex;
}

.sp-nav__item {
  font-weight: 700;
  line-height: 1.125;
  background-image: url(../images/seastar.png);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 1.125rem;
  padding-left: 1.375rem;
}

.sp-nav__item + .sp-nav__item {
  margin-top: 2.5rem;
}

.sp-nav__subitem {
  font-weight: 400;
  line-height: 1.125;
  margin-top: 1.5rem;
}

.button {
  background-color: #408f95;
  color: #fff;
  font-family: "Gotu";
  font-size: 0.875rem;
  line-height: 1.5714;
  padding: 1rem 2.5rem;
  position: relative;
  display: inline-block;
}

.button span {
  display: inline-block;
  content: "";
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
  margin-left: 0.5rem;
}

.button::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  border: 1px solid #408f95;
  right: -0.25rem;
  bottom: -0.25rem;
}

.card {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
  padding: 1rem 1rem 1.5rem;
  background-color: #fff;
}

.card__body {
  margin-top: 1.5rem;
}

.card__date {
  font-family: "Gotu";
  font-size: 0.75rem;
  line-height: 1.5;
}

.card__title {
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.5;
  padding-bottom: 0.25rem;
  border-bottom: 1px solid #408f95;
}

.card__text {
  margin-top: 1rem;
}

.card__inner {
  display: flex;
  align-items: flex-end;
}

.card__meta {
  margin-right: 0.8125rem;
}

.card__age {
  font-size: 0.75rem;
  line-height: 1.5;
}

.category-tag {
  font-size: 0.75rem;
  font-weight: 400;
  color: #408f95;
  line-height: 1.83333;
  border: 1px solid #408f95;
  text-align: center;
  padding: 0.25rem 0.75rem;
  font-weight: 400;
  display: inline-block;
}

.hamburger {
  width: 2rem;
  height: 1.5rem;
  position: relative;
  z-index: 999;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: absolute;
  width: inherit;
  height: 0.0625rem;
  background-color: #fff;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  top: 50%;
  opacity: 1;
}

.hamburger span:nth-child(3) {
  top: 100%;
}

.hamburger.is-active span:nth-child(1) {
  top: 60%;
  transform: rotate(45deg);
}

.hamburger.is-active span:nth-child(2) {
  top: 50%;
  opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
  top: 60%;
  transform: rotate(-45deg);
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.section-header {
  text-align: center;
}

.section-header__title {
