}

.section-header__subtitle {
  font-size: 0.75rem;
  color: #408f95;
  line-height: 1.5;
  margin-top: 0.25rem;
}

@media screen and (min-width: 768px) {
  .section-header--white {
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  .section-header--white::first-letter {
    color: #ddf0f1;
  }
}